      <textarea class="description" id="description">
          –ï—Å–ª–∏ –≤—ã –ø–æ—Ç–µ—Ä–ø–µ–≤—à–∏–π –∏ –Ω–∞–º–µ—Ä–µ–≤–∞–µ—Ç–µ—Å—å –æ–±—Ä–∞—Ç–∏—Ç—å—Å—è –≤ —Å—Ç—Ä–∞—Ö–æ–≤—É—é –∫–æ–º–ø–∞–Ω–∏—é –∑–∞ –≤–æ–∑–º–µ—â–µ–Ω–∏–µ–º —É—â–µ—Ä–±–∞, —Ç–æ —Å—Ç—Ä–∞—Ö–æ–≤–∞—è –∫–æ–º–ø–∞–Ω–∏—è –æ—Ä–≥–∞–Ω–∏–∑—É–µ—Ç –¥–ª—è –≤–∞—Å —ç–∫—Å–ø–µ—Ä—Ç–∏–∑—É. –û–¥–Ω–∞–∫–æ –¥–ª—è –¥—Ä—É–≥–∏—Ö —Ü–µ–ª–µ–π, –Ω–∞–ø—Ä–∏–º–µ—Ä, –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç–∏ —ç–∫—Å–ø–µ—Ä—Ç–∏–∑—ã, —Å–¥–µ–ª–∞–Ω–Ω–æ–π –ø–æ –∑–∞–∫–∞–∑—É —Å—Ç—Ä–∞—Ö–æ–≤–æ–π –∫–æ–º–ø–∞–Ω–∏–∏, –¥–ª—è –æ–±—Ä–∞—â–µ–Ω–∏—è –∫ –≤–∏–Ω–æ–≤–Ω–∏–∫—É –î–¢–ü –∑–∞ –≤–æ–∑–º–µ—â–µ–Ω–∏–µ–º —É—â–µ—Ä–±–∞ –∏–ª–∏ –≤ —Å—É–¥–µ–±–Ω—ã–µ –∏–Ω—Å—Ç–∞–Ω—Ü–∏–∏, –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —Å—Ç–æ–∏–º–æ—Å—Ç–∏ –≤–æ—Å—Å—Ç–∞–Ω–æ–≤–∏—Ç–µ–ª—å–Ω–æ–≥–æ —Ä–µ–º–æ–Ω—Ç–∞ –∏ –ø—Ä–æ—á–∏—Ö —Ü–µ–ª–µ–π, –≤–∞–º –º–æ–∂–µ—Ç –ø–æ–Ω–∞–¥–æ–±–∏—Ç—å—Å—è –ø–æ–ª–Ω–æ—Ü–µ–Ω–Ω–æ–µ, —Å–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω–æ–µ –∏ –æ—Ñ–æ—Ä–º–ª–µ–Ω–Ω–æ–µ –Ω–∞–¥–ª–µ–∂–∞—â–∏–º –æ–±—Ä–∞–∑–æ–º, —ç–∫—Å–ø–µ—Ä—Ç–Ω–æ–µ –∑–∞–∫–ª—é—á–µ–Ω–∏–µ. –ó–∞–∫–∞–∂–∏—Ç–µ —É –Ω–∞—Å –¥–∞–Ω–Ω—É—é —É—Å–ª—É–≥—É, –Ω–∞—à —ç–∫—Å–ø–µ—Ä—Ç –æ—Å–º–æ—Ç—Ä–∏—Ç –ø–æ–≤—Ä–µ–∂–¥–µ–Ω–Ω–æ–µ –∏–º—É—â–µ—Å—Ç–≤–æ –≤ —É–¥–æ–±–Ω–æ–º –¥–ª—è –≤–∞—Å –º–µ—Å—Ç–µ, –ø–æ—Å–ª–µ —á–µ–≥–æ —ç–∫—Å–ø–µ—Ä—Ç—ã-—Ç–µ—Ö–Ω–∏–∫–∏ –æ—Å—É—â–µ—Å—Ç–≤—è—Ç —Ä–∞—Å—á–µ—Ç —É—â–µ—Ä–±–∞ —Å –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ–º –ª–∏—Ü–µ–Ω–∑–∏—Ä–æ–≤–∞–Ω–Ω—ã—Ö –ø—Ä–æ–≥—Ä–∞–º–º–Ω—ã—Ö –∫–æ–º–ø–ª–µ–∫—Å–æ–≤.
