<script>
export default {
  name: "Expertcall",
  props: ['responseServer'],
  data() {
    return {
      map: '',
      message: '',
      show–°alc: true,
      cash: true,
      agree: false,
    }
  },
  methods: {
    getPosition() {
      const input = document.querySelector('#where');
      input.value = '';

      const options = {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
      }

      if (!navigator.geolocation) {
        this.message = '–ü–æ–∏—Å–∫ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏ –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è –≤ –≤–∞—à–µ–º –±—Ä–∞—É–∑–µ—Ä–µ.';
      } else {
        this.message = '–ü–æ–∏—Å–∫‚Ä¶';
        navigator.geolocation.getCurrentPosition((position) => {
          const latitude = position.coords.latitude;
          const longitude = position.coords.longitude;
          this.message = '';
          input.value = `Latitude: ${latitude} ¬∞, Longitude: ${longitude} ¬∞`;
          this.map = `https://www.openstreetmap.org/#map=18/${latitude}/${longitude}`;
        }, () => {
          this.message = '–†–∞–∑—Ä–µ—à–∏—Ç–µ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏';
        }, options);
      }
    },
    saveData: function(data)  {
      this.responseServer = data;
      this.$router.push('/done');
      this.$emit('responseSave', this.responseServer);
    },
    redirect: function () {
      let href = window.location.href + '/done';
      window.location.replace(href);
    },
    setCash() {
      this.cash = !this.cash;
    },
    sentData: function () {
      let phone = document.getElementById('phone1').value;
      let name = document.getElementById('name1').value;
      if (phone != '' && name != '' && phone.length === 14) {

        let form = document.forms.client1;
        let formData = new FormData(form); // —Å–æ–∑–¥–∞—ë–º –æ–±—ä–µ–∫—Ç, –ø–æ –∂–µ–ª–∞–Ω–∏—é –±–µ—Ä—ë–º –¥–∞–Ω–Ω—ã–µ —Ñ–æ—Ä–º—ã <form>
        formData.append('type', '—Ä–∞—Å—á–µ—Ç —É—â–µ—Ä–±–∞');
        let xhr = new XMLHttpRequest();
        // xhr.setRequestHeader('From', 'Delta');
// 2. –ù–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –µ–≥–æ: GET-–∑–∞–ø—Ä–æ—Å –ø–æ URL /article/.../load
        xhr.open('POST', 'http://127.0.0.1/');

// 3. –û—Ç—Å—ã–ª–∞–µ–º –∑–∞–ø—Ä–æ—Å
        xhr.send(formData);

// 4. –≠—Ç–æ—Ç –∫–æ–¥ —Å—Ä–∞–±–æ—Ç–∞–µ—Ç –ø–æ—Å–ª–µ —Ç–æ–≥–æ, –∫–∞–∫ –º—ã –ø–æ–ª—É—á–∏–º –æ—Ç–≤–µ—Ç —Å–µ—Ä–≤–µ—Ä–∞
        xhr.onload =  () => {
          if (xhr.status != 200) { // –∞–Ω–∞–ª–∏–∑–∏—Ä—É–µ–º HTTP-—Å—Ç–∞—Ç—É—Å –æ—Ç–≤–µ—Ç–∞, –µ—Å–ª–∏ —Å—Ç–∞—Ç—É—Å –Ω–µ 200, —Ç–æ –ø—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞
            alert(`–û—à–∏–±–∫–∞ ${xhr.status}: ${xhr.statusText}`); // –ù–∞–ø—Ä–∏–º–µ—Ä, 404: Not Found
          } else { // –µ—Å–ª–∏ –≤—Å—ë –ø—Ä–æ—à–ª–æ –≥–ª–∞–¥–∫–æ, –≤—ã–≤–æ–¥–∏–º —Ä–µ–∑—É–ª—å—Ç–∞—Ç
            this.saveData(xhr.response);
            // this.redirect();
            // response -- —ç—Ç–æ –æ—Ç–≤–µ—Ç —Å–µ—Ä–≤–µ—Ä–∞
          }
        };


        xhr.onerror = function () {
          alert("–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫:( –°–≤—è–∂–∏—Ç–µ—Å—å —Å –Ω–∞–º–∏ –ø–æ —Ç–µ–ª–µ—Ñ–æ–Ω—É –∏–ª–∏ –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑!");
        };
      }else {
        alert('–í–≤–µ–¥–∏—Ç–µ –∏–º—è –∏ —Ç–µ–ª–µ—Ñ–æ–Ω');
    }
    }
  }
}
</script>

