          –ù–∞—à —ç–∫—Å–ø–µ—Ä—Ç –ø—Ä–∏–µ–¥–µ—Ç –Ω–∞ –º–µ—Å—Ç–æ –î–¢–ü –≤ —Ç–µ—á–µ–Ω–∏–µ 30 –º–∏–Ω—É—Ç —Å –º–æ–º–µ–Ω—Ç–∞ –∑–∞–∫–∞–∑–∞ —É—Å–ª—É–≥–∏. –û–∫–∞–∂–µ—Ç –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏—é –ø–æ —Å–ø–æ—Å–æ–±—É –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏—è –î–¢–ü (—Å —É—á–∞—Å—Ç–∏–µ–º –ì–ò–ë–î–î –∏–ª–∏ –ï–≤—Ä–æ–ø—Ä–æ—Ç–æ–∫–æ–ª). –í –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Å–ø–æ—Å–æ–±–∞ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏—è –î–¢–ü –ø–æ–¥—Å–∫–∞–∂–µ—Ç –ø–æ—Ä—è–¥–æ–∫ –¥–µ–π—Å—Ç–≤–∏–π –ø–æ –ø—Ä–æ—Ü–µ–¥—É—Ä–µ –æ—Ñ–æ—Ä–º–ª–µ–Ω–∏—è –î–¢–ü (—Å—Ö–µ–º–∞, –ò–∑–≤–µ—â–µ–Ω–∏–µ, —Å–æ—Å—Ç–∞–≤–ª–µ–Ω–∏–µ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–∞–º–∏ –ì–ò–ë–î–î). –ü–æ–¥—Å–∫–∞–∂–µ—Ç –ø–æ—Ä—è–¥–æ–∫ –æ–±—Ä–∞—â–µ–Ω–∏—è –≤ —Å—Ç—Ä–∞—Ö–æ–≤—É—é –∫–æ–º–ø–∞–Ω–∏—é.
