<script>
export default {
  name: "Expertcall",
  props: ['responseServer'],
  data() {
    return {
      map: '',
      message: '',
      show: true,
      cash: true,
      agree: false,
      testForm: false,
    }
  },
  methods: {
    redirect: function () {
      let href = window.location.href + '/done';
      window.location.replace(href);
    },
    setCash() {
      this.cash = !this.cash;
    },
    saveData: function(data)  {
      this.responseServer = data;
      this.$router.push('/done');
      this.$emit('responseSave', this.responseServer);
    },
    sentData: function () {
      let phone = document.getElementById('phone').value;
      let name = document.getElementById('name').value;
      if (phone != '' && name != '' && phone.length === 14) {
        let form = document.forms.client;
        let formData = new FormData(form); // —Å–æ–∑–¥–∞—ë–º –æ–±—ä–µ–∫—Ç
        formData.append('type', '–≤—ã–µ–∑–¥');
        let xhr = new XMLHttpRequest();

// 2. –ù–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –µ–≥–æ: GET-–∑–∞–ø—Ä–æ—Å –ø–æ URL /article/.../load
        xhr.open('POST', 'http://127.0.0.1/');

// 3. –û—Ç—Å—ã–ª–∞–µ–º –∑–∞–ø—Ä–æ—Å
        xhr.send(formData);

// 4. –≠—Ç–æ—Ç –∫–æ–¥ —Å—Ä–∞–±–æ—Ç–∞–µ—Ç –ø–æ—Å–ª–µ —Ç–æ–≥–æ, –∫–∞–∫ –º—ã –ø–æ–ª—É—á–∏–º –æ—Ç–≤–µ—Ç —Å–µ—Ä–≤–µ—Ä–∞
        xhr.onload = () => {
          if (xhr.status != 200) { // –∞–Ω–∞–ª–∏–∑–∏—Ä—É–µ–º HTTP-—Å—Ç–∞—Ç—É—Å –æ—Ç–≤–µ—Ç–∞, –µ—Å–ª–∏ —Å—Ç–∞—Ç—É—Å –Ω–µ 200, —Ç–æ –ø—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞
            alert(`–û—à–∏–±–∫–∞ ${xhr.status}: ${xhr.statusText}`); // –ù–∞–ø—Ä–∏–º–µ—Ä, 404: Not Found
          } else { // –µ—Å–ª–∏ –≤—Å—ë –ø—Ä–æ—à–ª–æ –≥–ª–∞–¥–∫–æ, –≤—ã–≤–æ–¥–∏–º —Ä–µ–∑—É–ª—å—Ç–∞—Ç
            this.saveData(xhr.response);
            // this.redirect(); // response -- —ç—Ç–æ –æ—Ç–≤–µ—Ç —Å–µ—Ä–≤–µ—Ä–∞
          }
        };

        xhr.onprogress = (event) => {
          if (event.lengthComputable) {
            alert(`–ü–æ–ª—É—á–µ–Ω–æ ${event.loaded} –∏–∑ ${event.total} –±–∞–π—Ç`);
          } else {
            return true // –µ—Å–ª–∏ –≤ –æ—Ç–≤–µ—Ç–µ –Ω–µ—Ç –∑–∞–≥–æ–ª–æ–≤–∫–∞ Content-Length
          }

        };

        xhr.onerror = function () {
          alert("–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫:( –°–≤—è–∂–∏—Ç–µ—Å—å —Å –Ω–∞–º–∏ –ø–æ —Ç–µ–ª–µ—Ñ–æ–Ω—É –∏–ª–∏ –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑!");
        };
      } else {
        alert('–í–≤–µ–¥–∏—Ç–µ –∏–º—è –∏ —Ç–µ–ª–µ—Ñ–æ–Ω');
      }
    }
  }
}
</script>

