<template>
  <div :class="{app: true, flex_Full: fullScreen}">
    <div class="left" v-if="fullScreen"> <!-- add for full screen -->
      <Menufull :fullScreen="fullScreen" :arrow="showArrow"></Menufull>
    </div>
    <div class="right">
      <Up v-if="!fullScreen"></Up>
      <router-view :fullScreen="fullScreen" :responseServer="responseServer" @responseSave="responseChange"></router-view>
      <h1>{{responseServer}}</h1>
      <Services></Services>
      <Faq></Faq>
      <Evro></Evro>
      <AboutBig :full-screen="fullScreen"></AboutBig>
      <Contact></Contact>
      <FooterDelta></FooterDelta>
    </div>
  </div>
</template>


<script>
import Up from "./components/Up";
import Faq from "@/components/Faq";
import Evro from "@/components/Evro";
import Contact from "@/components/Contact";
import FooterDelta from "@/components/Footer-delta";
import AboutBig from "@/components/AboutBig";
import Services from "@/components/Services";
import Menufull from "@/components/Menufull";

export default {
  name: 'App',
  data() {
    return {
      fullScreen: false,
      showArrow: false,
      responseServer: ''
    }
  },
  created() { //add for full screen
    if (document.documentElement.clientWidth < 1365 && this.fullScreen === true) {
      this.fullScreen = false;
    }else if(document.documentElement.clientWidth > 1365 && this.fullScreen === false) {
      this.fullScreen = true;
    }
    window.addEventListener('resize', ()=> {
      if (document.documentElement.clientWidth < 1365 && this.fullScreen === true) {
        this.fullScreen = false;
      }else if(document.documentElement.clientWidth > 1365 && this.fullScreen === false) {
        this.fullScreen = true;
      }
      if (document.documentElement.clientWidth < 1366 && this.fullScreen === true) {
        return document.location.reload();
      }else if (document.documentElement.clientWidth > 1366 && this.fullScreen === false) {
        return document.location.reload();
      }else {
        return true;
      }
    });
    if (this.fullScreen === true) {
      window.addEventListener('scroll', () => {
        if (window.pageYOffset >= 50) {
          this.showArrow = true;
        } else {
          this.showArrow = false;
        }
      });
    }
    let SupportsCSS = function (property, value) {
      // –°–æ–∑–¥–∞—ë–º —ç–ª–µ–º–µ–Ω—Ç
      let element = document.createElement('span');
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç –ª–∏ –±—Ä–∞—É–∑–µ—Ä –¥–∞–Ω–Ω–æ–µ —Å–≤–æ–π—Å—Ç–≤–æ
      if (element.style[property] !== undefined)
        element.style.cssText = property + ':' + value; // –í–Ω–æ—Å–∏–º –Ω–æ–≤–æ–µ —Å–≤–æ–π—Å—Ç–≤–æ –≤ style —ç–ª–µ–º–µ–Ω—Ç–∞
      else
        return false; // –ï—Å–ª–∏ –±—Ä–∞—É–∑–µ—Ä –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç —Å–≤–æ–π—Å—Ç–≤–æ, —Ç–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º false
      // –ï—Å–ª–∏ –±—Ä–∞—É–∑–µ—Ä –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç –¥–∞–Ω–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –¥–ª—è —É–∫–∞–∑–∞–Ω–Ω–æ–≥–æ —Å–≤–æ–π—Å—Ç–≤–∞, —Ç–æ –∑–Ω–∞—á–µ–Ω–∏—è –±—É–¥—É—Ç —Ä–∞–≤–Ω—ã
      return element.style[property] === value;
    };
    if (SupportsCSS('position', 'sticky')) {
      return true
    } else {
      return document.getElementById('forIe').style.display = 'block';
    }
  },
  components: {
    Menufull, //add for full screen
    Services,
    AboutBig,
    FooterDelta,
    Contact,
    Evro,
    Faq,
    Up,
  },
  methods: {
    responseChange: function(data) {
      console.log(data);
       this.responseServer = data;
    }
  }
}
</script>


