<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

export default {
  created() {
    if (this.fullScreen) {
      this.settings['slidesToShow'] = 2;
      this.settings['slidesToScroll'] = 2;
    }else {
      this.settings['slidesToShow'] = 1;
      this.settings['slidesToScroll'] = 1;
    }
  },
  name: 'SliderMain',
  components: {VueSlickCarousel},
  props: ['fullScreen'],
  data() {
    return {
      settings: {
        "arrows": false,
        "dots": true,
        "dotsClass": "slick-dots custom-dot-class",
        "edgeFriction": 0.35,
        "infinite": true,
        "speed": 500,
        "slidesToShow": 1,
        "slidesToScroll": 1,
        "autoplay": true,
        "autoplaySpeed": 25000,
        "adaptiveHeight": true
      },
      block: {
        "ak": false,
        "help": false,
        "calc": false,
        "onlineCalc": false
      }
    }
  },
  methods: {
    show: function (key) {
      for (let blockKey in this.block) {
        if (blockKey === key) {
          this.block[blockKey] = true
        } else {
          this.block[blockKey] = false
        }
      }
    }
  }
}
